.input-group {
    display: flex;
    flex-direction: column;
    font-weight: 300;
    font-size: 1.1rem;
}
.input-group:not(:last-child) {
    margin-bottom: 1.5em;
}
.input-group:not(:first-child) {
    margin-top: 1em;
}
.input-group input {
    margin-top: 1em;
    border: unset;
    border-bottom: 1px solid rgba(0, 0, 0, 1);
    padding: .5em;
}